@font-face {
  font-family: "Roboto-Thin";
  src: url("./configs/fonts/Mulish/Mulish-ExtraLight.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-Thin-Italic";
  src: url("./configs/fonts/Mulish/Mulish-ExtraLightItalic.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-Light";
  src: url("./configs/fonts/Mulish/Mulish-Light.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-LightItalic";
  src: url("./configs/fonts/Mulish/Mulish-LightItalic.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-Regular";
  src: url("./configs/fonts/Mulish/Mulish-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-Italic";
  src: url("./configs/fonts/Mulish/Mulish-Italic.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-Medium";
  src: url("./configs/fonts/Mulish/Mulish-SemiBold.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-MediumItalic";
  src: url("./configs/fonts/Mulish/Mulish-SemiBoldItalic.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-Bold";
  src: url("./configs/fonts/Mulish/Mulish-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-BoldItalic";
  src: url("./configs/fonts/Mulish/Mulish-BoldItalic.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-Black";
  src: url("./configs/fonts/Mulish/Mulish-Black.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto-BlackItalic";
  src: url("./configs/fonts/Mulish/Mulish-BlackItalic.ttf") format("truetype");
}

:root {
  --roboto-100: Roboto-Thin;
  --roboto-100-i: Roboto-Thin-Italic;
  --roboto-300: Roboto-Light;
  --roboto-300-i: Roboto-LightItalic;
  --roboto-400: Roboto-Regular;
  --roboto-400-i: Roboto-Italic;
  --roboto-500: Roboto-Medium;
  --roboto-500-i: Roboto-MediumItalic;
  --roboto-700: Roboto-Bold;
  --roboto-700-i: Roboto-BoldItalic;
  --roboto-900: Roboto-Black;
  --roboto-900-i: Roboto-BlackItalic;
}

html {
  font-size: 16px;
}
body {
  margin: 0;
  font-family: var(--roboto-400), sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ant-table-thead > tr > th {
  font-family: var(--roboto-500);
}
.ant-drawer-title {
  font-family: var(--roboto-500);
}

.ant-modal-title {
  font-family: var(--roboto-500);
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

.ant-input[disabled] {
  color: #8c8c8c !important;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #efefef;
  border-radius: 20px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #f7f3f3;
  border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #d8d6d6;
}

.notification-global {
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  width: 446px;

  .ant-notification-notice-icon {
    top: 50%;
    font-size: 31px;
    transform: translateY(-50%);
  }

  .ant-notification-notice-message {
    color: #252424;
    font-family: var(--roboto-500);
    font-size: 18px;
    padding-left: 7px;
    text-transform: capitalize;
  }

  .ant-notification-notice-description {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    color: #bfbfbf;
    font-size: 16px;
    padding-right: 30px;
    padding-left: 7px;
  }
}

.ant-notification-notice-close {
  top: 50%;
  transform: translateY(-50%);
}

