.customDrawerEmail {
  .sun-editor .se-wrapper .se-wrapper-inner {
    max-height: 480px;
    height: unset !important;
  }
}

.ant-breadcrumb {
  ol {
    align-items: center;
  }
}

.ql-font-arial,
.ql-font span[data-value="arial"]::before {
  font-family: Arial, sans-serif;
}
.ql-font-comic-sans,
.ql-font span[data-value="comic-sans"]::before {
  font-family: "Comic Sans MS", cursive, sans-serif;
}
.ql-font-courier-new,
.ql-font span[data-value="courier-new"]::before {
  font-family: "Courier New", sans-serif;
}
.ql-font-georgia,
.ql-font span[data-value="georgia"]::before {
  font-family: Georgia, serif;
}
.ql-font-helvetica,
.ql-font span[data-value="helvetica"]::before {
  font-family: Helvetica, sans-serif;
}
.ql-font-lucida,
.ql-font span[data-value="lucida"]::before {
  font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
}

/* Set content for sizes */
.ql-size-extra-small,
.ql-size span[data-value="extra-small"]::before {
  font-size: 13px !important;
}
.ql-size-small,
.ql-size span[data-value="small"]::before {
  font-size: 14px !important;
}
.ql-size-medium,
.ql-size span[data-value="medium"]::before {
  font-size: 18px !important;
}
.ql-size-large,
.ql-size span[data-value="large"]::before {
  font-size: 20px !important;
}
.ql-size-very-large,
.ql-size span[data-value="very-large"]::before {
  font-size: 24px !important;
}
.react-flow__node {
  z-index: -1 !important;
}
.react-flow__edge-path {
  stroke-width: 2 !important;
}
::-webkit-resizer {
  visibility: hidden;
}
.ag-font-style {
  user-select: initial;
  -moz-user-select: text;
  -ms-user-select: text;
  -webkit-user-select: text;
}
::-webkit-scrollbar {
  height: 8px !important;
}

.object-view {
  background: #ffffff;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.16);
  border-radius: 5px;
}

.tox-notification {
  display: none !important;
}

.custom_popover {
  .ant-popover-inner-content {
    padding: 0px;
  }
}

.new_detail_crm {
  .ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
    font-weight: unset;
    background-color: #fff;
  }
  .ant-select-item-option-selected:not(.ant-select-item-option-disabled):hover {
    background-color: #f5f5f5;
  }

  .ant-select-item-option-state {
    display: none;
  }
}


.popoverProfile  {
  top: 55px !important;
  .ant-popover-content {
    .ant-popover-inner {
      border-radius: 12px;
      .ant-popover-inner-content {
        padding: 0;
        div {
          .ant-row {
            margin: 0;
          }

          .ant-divider {
            margin: 8px 0;
          }
        }
      }
    }
  }
}

.popupTableAG {
  .ant-popover-content {
    .ant-popover-inner {
      border-radius: 6px;
    }
  }
}

.ant-menu-submenu-placement-rightTop {
