.popoverNoti {
  position: fixed !important;

  .ant-popover-content {
    height: 100;
    max-height: 720px !important;
    min-height: 200px !important;
    width: 390px;
  }

  .ant-popover-inner {
    height: 100%;
    max-height: 720px !important;
    min-height: 200px !important;
    overflow-y: auto;
    border-radius: 12px;

    .ant-popover-inner-content {
      height: 100%;
      max-height: 720px !important;
      min-height: 200px !important;
      padding: 0;
    }
  }

  .ant-popover-title {
    text-align: center;
  }
  .ant-popover-inner-content {
    width: unset !important;
  }
}

.popoverMenuNoti {
  border-radius: 2px;

  .ant-popover-inner-content {
    padding: 4px 0px;
  }

  .ant-popover-inner {
    border-radius: 2px;
  }
}


.popoverSidebar {
  .ant-popover-content {
    .ant-popover-inner {
      border-radius: 6px;
      .ant-popover-inner-content {
        padding: 12px 0px;
      }
    }
   }
}